.remove-btn{
    margin-right: 0.5rem;
}

.fade-enter{
    opacity: 0.01;
}

.fade-enter-active{
    opacity: 1;
    -webkit-transition: opacity 1000ms ease-in;
    transition: opacity 1000ms ease-in;
}

.fade-exit{
    opacity: 1;
}

.fade-exit-active{
    opacity: 0.01;
    -webkit-transition: opacity 1000ms ease-out;
    transition: opacity 1000ms ease-out;
}
